@import url(https://fonts.googleapis.com/css?family=Teko&display=swap);
.home{margin-top:100px;text-align:center}.home .name{font-size:3rem;font-weight:bold;margin-top:15px;-webkit-animation:an 1s ease-out 1 both;animation:an 1s ease-out 1 both}.home .name span{-webkit-animation:coloring 10s infinite;animation:coloring 10s infinite}@-webkit-keyframes an{from{opacity:0;-webkit-transform:perspective(500px) translate3d(-35px, -40px, -150px) rotate3d(1, -1, 0, 35deg);transform:perspective(500px) translate3d(-35px, -40px, -150px) rotate3d(1, -1, 0, 35deg)}to{opacity:1;-webkit-transform:perspective(500px) translate3d(0, 0, 0);transform:perspective(500px) translate3d(0, 0, 0)}}@keyframes an{from{opacity:0;-webkit-transform:perspective(500px) translate3d(-35px, -40px, -150px) rotate3d(1, -1, 0, 35deg);transform:perspective(500px) translate3d(-35px, -40px, -150px) rotate3d(1, -1, 0, 35deg)}to{opacity:1;-webkit-transform:perspective(500px) translate3d(0, 0, 0);transform:perspective(500px) translate3d(0, 0, 0)}}@-webkit-keyframes coloring{0%{color:#4e0ec4}15%{color:#422180}35%{color:#261940}55%{color:#3c3b3d}75%{color:#58357a}90%{color:#6825a8}100%{color:#6f17c2}}@keyframes coloring{0%{color:#4e0ec4}15%{color:#422180}35%{color:#261940}55%{color:#3c3b3d}75%{color:#58357a}90%{color:#6825a8}100%{color:#6f17c2}}.home .job{font-family:'Teko', sans-serif;font-weight:bold;font-size:2rem;-webkit-animation:slidein 2s;animation:slidein 2s}@-webkit-keyframes slidein{0%{margin-left:-800px;opacity:0}20%{margin-left:-800px;opacity:0}35%{margin-left:0px;opacity:1}100%{margin-left:0px;opacity:1}}@keyframes slidein{0%{margin-left:-800px;opacity:0}20%{margin-left:-800px;opacity:0}35%{margin-left:0px;opacity:1}100%{margin-left:0px;opacity:1}}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

